import React, {useState, useEffect} from 'react';
import {useIntl} from 'react-intl';
import AppsContainer from '../../../@crema/core/AppsContainer';
// import AppComponentCard from '@crema/core/AppComponentCard';
import {AppComponentHeader} from '@crema';
import {IconButton, TextField, Tooltip} from '@mui/material';
import {Box} from '@mui/system';
import {MobileDateRangePicker} from '@mui/lab';
import ReactToPdf from 'react-to-pdf';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import {useLazyQuery} from '@apollo/client';
import {GET_PRODUCT_REPORT} from 'query/orderReoprt/getOrder';

import CAppContaner from 'components/CappContaner';
import {getWearhouseRebort} from '../../../@crema/services/auth/firebase/firebase';
// import ProductList from 'pages/ecommerce/Products/ProductListing/ProductList';
const WarehouseReports = () => {
  const ref = React.createRef();
  const [getProductRebort, {loading, error, data}] =
    useLazyQuery(GET_PRODUCT_REPORT);

  const {messages} = useIntl();
  // const [isLoading,setIsLoding]=useState(true);
  const [product, getProduct] = useState([]);
  const [value, setValue] = React.useState([new Date(), new Date()]);
  useEffect(() => {
    // setIsLoding(false);
  }, [product]);
  useEffect(() => {
    console.log('data', data);
    console.log(error);
  }, [loading]);
  const handleSubmit = async () => {
    let firstDate = '';
    let seconDate = '';
    // setIsLoding(true);
    if (value[0] != null) firstDate = dateForm(value[0]);
    if (value[1] != null) seconDate = dateForm(value[1]);

    getProduct(await getWearhouseRebort(value));
    getProductRebort({
      variables: {startDate: firstDate, endDate: seconDate},
    });
  };
  const dateForm = (date) => {
    var dd = String(date.getDate()).padStart(2, '0');
    var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = date.getFullYear();
    date = mm + '/' + dd + '/' + yyyy;
    return date;
  };
  return (
    <div ref={ref}>
      <>
        <AppComponentHeader
          cardStyle={{height: 'auto'}}
          fullView
          title={messages['sidebar.reports.warehouse']}
        />
        <CAppContaner
          title={messages['Datepicker.title']}
          buttonCompMethod={handleSubmit}
          component={() => (
            <>
              <MobileDateRangePicker
                startText={messages['sidebar.reports.startdate']}
                endText={messages['sidebar.reports.enddate']}
                value={value}
                onChange={(newValue) => {
                  setValue(newValue);
                }}
                renderInput={(startProps, endProps) => (
                  <React.Fragment>
                    <TextField {...startProps} />
                    <Box sx={{mx: 2}}> to </Box>
                    <TextField {...endProps} />
                  </React.Fragment>
                )}
                buttonComp={() => {}}
              />
            </>
          )}
        />
        <div>
          {<AppsContainer fullView></AppsContainer>}

          <Box textAlign={'end'} id='downloadPdf'>
            <ReactToPdf
              targetRef={ref}
              filename='div-blue.pdf'
              x={0}
              y={-0.4}
              scale={0.8}
            >
              {({toPdf}) => (
                <Tooltip title='Download PDF'>
                  <IconButton onClick={toPdf}>
                    <FileDownloadIcon color='primary' />
                  </IconButton>
                </Tooltip>
              )}
            </ReactToPdf>
          </Box>
        </div>
      </>
    </div>
  );
};
export default WarehouseReports;
